.button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: none;
  border: 4px solid #ffffff;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-decoration: none; }

.promo {
  background: url("../img/promo-background.png") no-repeat;
  background-size: 100% 100%; }

.promo__wrapper {
  display: flex;
  flex-direction: row;
  max-width: 1366px;
  margin: 0 auto;
  padding-bottom: 44px; }

.promo__text-block {
  display: flex;
  flex-direction: column;
  width: 298px;
  margin-left: 201px;
  margin-top: 114px;
  position: relative; }

.promo__text-block span {
  font-family: "Bebas Neue", "Arial", sans-serif;
  letter-spacing: 0.03em;
  text-align: right;
  color: #ffffff; }

.promo__text-block span:first-child {
  font-size: 49px;
  line-height: 49px; }

.promo__text-block span:nth-child(2) {
  font-size: 109px;
  line-height: 109px;
  padding-top: 7px; }

.promo__list {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif; }

.promo__list.features {
  position: relative;
  bottom: 86px;
  left: -264px;
  text-align: right;
  font-size: 14px;
  line-height: 150%; }

.promo__list.social {
  align-self: flex-end;
  margin-left: auto;
  margin-right: 102px;
  margin-bottom: 30px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 14px; }

.promo__list.social li {
  margin-bottom: 19px; }

.promo__list.social a {
  text-decoration: none;
  color: #ffffff; }

.promo__list.social a:hover {
  text-decoration: underline; }

.promo__image {
  margin-left: 63px; }

.promo__button-link {
  color: #fff;
  width: 234px;
  height: 60px;
  padding: 0 15px;
  margin-left: 65px;
  margin-top: -1px; }

.promo__button-link:hover {
  background-color: #ffffff;
  color: #102654; }

.header__tel-link:hover {
  text-decoration: underline; }

@media (max-width: 1199px) {
  .promo__wrapper {
    justify-content: center;
    padding-bottom: 78px; }
  .promo__text-block {
    margin-left: 72px;
    margin-top: 74px; }
  .promo__text-block span:first-child {
    font-size: 34px;
    line-height: 34px; }
  .promo__text-block span:nth-child(2) {
    font-size: 86px;
    line-height: 86px; }
  .promo__list.features {
    bottom: 80px;
    left: -212px; }
  .promo__list.social {
    display: none; }
  .promo__image {
    margin-left: 29px;
    margin-top: 21px; }
  .promo__image img {
    width: 324px;
    height: 390px; }
  .promo__button-link {
    width: 187px;
    margin-left: 112px;
    margin-top: -22px; } }

@media (max-width: 769px) {
  .promo__wrapper {
    justify-content: start; } }

@media (max-width: 767px) {
  .promo__wrapper {
    justify-content: center;
    padding: 0 15px;
    padding-bottom: 86px; }
  .promo__text-block {
    margin-left: -9px;
    margin-top: 75px; }
  .promo__text-block span:first-child {
    font-size: 28px;
    line-height: 28px; }
  .promo__text-block span:nth-child(2) {
    font-size: 78px;
    line-height: 78px; }
  .promo__image {
    display: none; }
  .promo__list.features {
    bottom: 70px;
    left: -177px; }
  .promo__button-link {
    margin-top: -16px;
    width: 293px;
    margin-left: 7px;
    width: 292px; } }

.header__wrapper {
  display: flex;
  flex-direction: row;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
  padding-top: 28px; }

.header__wrapper a {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #ffffff; }

.header__tel-link {
  margin-left: auto; }

@media (max-width: 1199px) {
  .header__wrapper {
    padding: 37px 45px 0; }
  .header__wrapper a {
    font-size: 24px;
    line-height: 24px; } }

@media (max-width: 767px) {
  .header__wrapper {
    padding: 18px 15px 0; }
  .header__wrapper a {
    font-size: 20px;
    line-height: 20px; } }

.gym__wrapper {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  padding-left: 163px;
  padding-right: 103px;
  padding-bottom: 500px;
  padding-top: 120px;
  position: relative; }

.gym__text-block h2 {
  padding-top: 45px;
  margin: 0;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: #102654; }

.gym__text-block p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654; }

.gym__text-block p {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300; }

.gym__text-block p:first-of-type {
  padding-top: 10px;
  font-size: 18px;
  line-height: 27px; }

.gym__text-block p:nth-of-type(2) {
  padding-top: 39px; }

.gym__text-block p:nth-of-type(3) {
  padding-top: 8px; }

.gym__text-block {
  width: 360px; }

.gym__video-play {
  width: 72px;
  height: 72px;
  position: absolute;
  background: linear-gradient(44.89deg, #003D85 25.38%, #B80027 74.89%);
  border-radius: 50%;
  border: none;
  top: 51%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3; }

.gym__video-preview {
  position: absolute;
  top: 39.8%;
  left: 39.5%;
  width: 364px;
  height: 228px;
  background: url("../img/gym-video-preview@1x.jpg") no-repeat;
  background-position: 50% 50%;
  z-index: 2; }

.gym__video-preview::after {
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  background-color: rgba(20, 54, 122, 0.5); }

.gym__video-play::after {
  position: absolute;
  content: "";
  width: 100px;
  height: 100px;
  top: -15px;
  left: -14px;
  background: url("../img/play-ellipse.png") no-repeat; }

.gym__video-play::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 17px;
  top: 27px;
  left: 30px;
  background: url("../img/icon-play.svg") no-repeat; }

.gym__equipment-img {
  margin-left: 80px; }
